body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.column {
  float: left;
  width: 33.33%;
  padding: 5px;
}

/* Clear floats after image containers */

.row::after {
  content: "";
  clear: both;
  display: table;
}

.center {
  margin-left: auto;
  margin-right: auto;
}

.btn {
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  border: 2px solid #e74c3c;
  border-radius: 0.6em;
  color: #e74c3c;
  cursor: pointer;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1;
  margin: 20px;
  /* margin-top: -10vh;
  margin-right: 5vh; */
  z-index: 999;
  padding: 1.6em 2.8em;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}

.btn:hover, .btn:focus {
  color: #fff;
  outline: 0;
}

.dropBox {
  margin-right: 5vh;
  margin-left : 5vh;
  width: 100%;
  max-width: 50vh;
  max-height: 30vh;
  border: 3px dashed white;
}

.fourth {
  border-color: black;
  background-color: #f1c40f;
  color: black;
  background-image: linear-gradient(45deg, black 50%, transparent 50%);
  background-position: 100%;
  background-size: 400%;
  transition: background 300ms ease-in-out;
}

.fourth:hover {
  background-position: 0;
}

.questionImage {
  width: 15vh;
  max-width: 100px;
}

.answerButton {
  font-size: 2vw;
  height: 5vh;
  line-height: 0vh;
  vertical-align: middle;
  /* margin: 2vh; */
  padding: 3vh;
  position: relative;
}

.objectLeft, .objectRight {
  float: left;
}

.objectRow {
  margin-top: 10vh;
}
.parentDiv {
  margin-top: 20vh;
  width: 100%;
}


@media only screen and (max-width: 700px) {
  .objectLeft, .objectRight {
    float: left;
    width: 80%;
    text-align: center;
  }
  .objectLeft {
    margin-left: 10%;
  }
  .objectRight {
    margin-left: 10%;
  }
  .operator {
    width: 100%;
    float: left;
    text-align: center;
    line-height: "10px";
    height: "25px";
  }
  .questionImage {
    width: 14vw;
  }
  .draggableImage {
    width: 10vw;
  }
}




