.thought {
  display: -webkit-flex;
  display: flex;
  background: rgba(0, 0, 0, 0.3);
  padding: 20px;
  width: 50%;
  height: 10%;
  max-width: 800px;
  border-radius: 30px;
  min-width: 200px;
  min-height: 40px;
  position: absolute;
  top : 13vh;
  left: 0;
  right: 0;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}

.thought:before, .thought:after {
  content: "";
  background-color: #fff;
  border-radius: 50%;
  display: block;
  position: absolute;
  z-index: -1;
}

.thought:before {
  width: 44px;
  height: 44px;
  top: -12px;
  left: 28px;
  box-shadow: -50px 30px 0 -12px #fff;
}

.thought:after {
  bottom: -10px;
  right: 26px;
  width: 30px;
  height: 30px;
  box-shadow: 40px -34px 0 0 #fff, -28px -6px 0 -2px #fff, -24px 17px 0 -6px #fff, -5px 25px 0 -10px #fff;
}
